<template>
    <hr class="my-4" />
    <!-- 공지 번호 보여주는칸 -->
    <div id="noticeNum"> 
        <p class="text-start">{{ notice.nonum }}</p>
    </div>
    <!-- 공지 제목 보여주는칸 -->
    <div id="noticeTitle"> 
        <h3>{{ notice.notitle }}</h3>
    </div>
    <!-- 공지게시 시간 보여주는칸 -->
    <div id="noticeDate">
        <span >{{ notice.nodate }}</span>
    </div>
    <hr class="my-4" />
    <!-- 공지 내용 보여주는칸 -->
    <div id="noticeContent">
        <span>{{ notice.nocon }}</span>
    </div> 
    <hr class="my-4" />      
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            notice: {} // 공지사항 정보를 저장할 객체
        };
    },
    mounted() {
        this.fetchNoticeDetails();
    },
    methods: {
        async fetchNoticeDetails() {
            try {
                // HTTP POST 요청을 통해 공지사항 세부 정보를 가져옴
                const response = await axios.post('/JY_C_NoticeDetails', { nonum: this.$route.query.nonum }); 

                // 가져온 세부 정보를 저장
                this.notice = {
                    nonum: Number(response.data.nonum),
                    notitle: response.data.notitle,
                    nodate: new Date(response.data.nodate),
                    nocon: response.data.nocon
                };

                console.log('Response:', response.data);

            } catch (error) {
                console.error('Error fetching notice details:', error);
            }

        }
    }
}
</script>